import {Component, OnInit} from '@angular/core';

@Component({
    selector: 'mk-navigation-event',
    template: require('./navigation-event.component.html'),
    styles: [require('./navigation-event.component.scss')]
})
export class NavigationEventComponent implements OnInit {

    constructor() {
    }

    ngOnInit() {
    }

    onClick(target) {
        this.scrollElement(target);
    }

    public scrollElement(target) {
        let element = document.getElementById(target);
        window.scrollTo(0, element.offsetTop);
    }

}



